@import 'sg.less';
@dark_blue: #130e27;
@white: #fff;
@black: #353535;
@bg_red: #c62314;
@grey: #efefef;
@ff: Arial,
Helvetica,
sans-serif;
.button() {
    width: 100%;
    padding: 0;
    text-transform: uppercase;
    font-weight: 400;
    cursor: pointer;
}

.img_circle() {
    background: url(../img/bg_circle.png);
    width: 105px;
    height: 105px;
    border-radius: 50%;
    box-shadow: 0 0 16px 0 rgba(128, 128, 128, 0.71);
}

.button_red() {
    .button();
    color: @white;
    background: @bg_red;
}

.button_dark_blue {
    .button();
    color: @white;
    background: @dark_blue;
}

.reset();
.wrapper {
    .wrapper();
}

body {
    font-family: @ff;
}

a {
    text-decoration: none;
    color: inherit;
}

input,
button {
    border-style: none;
}

header {
    font-family: @ff;
    font-weight: 400;
    background: url(../img/bg-img.png) no-repeat top center/cover, @bg_red;
    color: @white;
    nav {
        padding-top: 40px;
        display: flex;
        ul {
            display: flex;
            margin-left: auto;
            li {
                padding: 0 16px;
                font-size: 18px;
                margin: auto 5px;
                text-transform: uppercase;
                padding: 5px 12px;
                &:hover {
                    background: @dark_blue;
                    box-shadow: 0 0 0 2px @dark_blue;
                    border-radius: 3px;
                    cursor: pointer;
                }
            }
        }
    }
}

.row_header {
    height: 524px;
    margin-top: 122px;
    .row-flex();
    .main_title {
        .col();
        .size(6);
        margin: 15px auto auto 20px;
        h1 {
            font-size: 90px;
            font-weight: bold;
            text-transform: uppercase;
            span {
                color: @dark_blue;
            }
        }
        p {
            font-size: 20px;
            line-height: 1.5;
        }
    }
    .form {
        .col();
        .size(5);
        background: @white;
        box-shadow: 0 0 38px 0px rgba(0, 0, 0, 0.71);
        border-radius: 5px;
        color: @dark_blue;
        margin-bottom: -10px;
        h2 {
            text-align: center;
            font-size: 22px;
            margin-top: 37px;
            margin-bottom: 42px;
            text-transform: uppercase;
            font-weight: 600;
        }
        form {
            display: flex;
            flex-direction: column;
            flex-wrap: nowrap;
            .form_buttom,
            .input_form {
                .col();
                .size(11);
                margin: 7px auto;
            }
            .input_form,
            button {
                height: 58px;
                background: #eee;
                border-radius: 5px;
            }
            button {
                .button_red();
                font-size: 22px;
            }
            .checkbox {
                .col();
                .size(11);
                display: flex;
                margin: 8px auto;
                height: 30px;
                align-items: center;
                .input_checkbox {
                    align-self: center;
                    width: 20px;
                    height: 20px;
                    margin-right: 20px;
                }
                p {
                    font-size: 20px;
                }
            }
        }
    }
}

.services {
    margin-top: -84px;
    background: @white;
    padding-top: 45px;
    h2 {
        font-size: 50px;
        font-weight: bold;
        text-transform: uppercase;
        font-family: @ff;
        span {
            color: @bg_red;
        }
    }
    .block {
        .row-flex();
        justify-content: center;
        margin-top: 95px;
        padding-bottom: 70px;
        .price {
            .col();
            .size(2);
            display: flex;
            flex-direction: column;
            align-items: center;
            .item {
                text-align: center;
                margin-top: 40px;
                font-size: 18px;
                font-family: @ff;
            }
            .shift(0.5);
        }
    }
}

.our_store {
    background: @dark_blue;
    .our_store_block {
        .row-flex();
        color: @white;
        padding-top: 33px;
        padding-bottom: 33px;
        justify-content: space-between;
        p {
            .col();
            .size(6);
        }
        .buttom {
            display: flex;
            .col();
            .size(3);
            padding: 25px auto;
            button {
                border-radius: 5px;
                .button_red();
                font-size: 20px;
            }
        }
    }
}

.pricing {
    background: @grey;
    padding-top: 65px;
    h2 {
        font-size: 50px;
        text-transform: uppercase;
        text-align: center;
        padding-bottom: 65px;
    }
    .block {
        .margin() {
            margin: 18px 0
        }
        text-align: center;
        .row-flex();
        justify-content: center;
        .item {
            display: flex;
            background: @white;
            border-radius: 5px;
            flex-direction: column;
            justify-content: flex-end;
            .col();
            .size(3.9);
            .shift-padding(0.6);
            .shift(0.05);
            .button {
                margin-top: auto;
                button {
                    .button_dark_blue();
                    font-size: 22px;
                    padding: 15px 5px;
                    border-radius: 5px;
                }
            }
            .price,
            h3,
            .text {
                .margin();
            }
            .price {
                color: @bg_red;
                font-size: 60px;
                font-weight: bold;
                text-transform: uppercase;
            }
            h3 {
                font-size: 22px;
                text-transform: capitalize;
            }
            .text {
                text-transform: capitalize;
                font-size: 20px;
            }
        }
        .popular {
            position: relative;
            .button {
                button {
                    .button_red();
                    font-size: 22px;
                    padding: 15px 5px;
                    border-radius: 5px;
                }
            }
        }
    }
}

.testimonial_gallery {
    padding-top: 147px;
    padding-bottom: 73px;
    .block {
        .row-flex();
        justify-content: space-between;
        h2 {
            text-align: center;
            font-size: 50px;
            text-transform: uppercase;
        }
        .testimonial_block {
            .col();
            .size(6);
            h2 {
                color: @dark_blue;
            }
            .item {
                display: flex;
                margin: 48px 0;
                .text {
                    .size(9);
                    padding: 28px 60px 28px 10px;
                    ;
                    border-radius: 5px;
                    p {
                        margin-top: 15px;
                    }
                }
                img {
                    align-self: center;
                    margin-top: 18px;
                    margin-bottom: 18px;
                    margin-left: -60px;
                    .img_circle();
                }
            }
            .item:nth-child(even) {
                .text {
                    background: @grey;
                }
                .title {
                    color: @bg_red;
                }
            }
        }
        .gallery_block {
            .col();
            .size(6);
            h2 {
                color: @bg_red;
            }
            .gallery_items {
                .row-flex();
                margin-top: 48px;
                justify-content: flex-end;
                img {
                    .col();
                    .size(4);
                    .shift-left(1);
                    .img_circle();
                    margin-top: 18px;
                    margin-bottom: 18px;
                }
            }
            a {
                width: 100%;
                display: block;
                text-align: center;
                margin-top: 42px;
                font-size: 20px;
                text-transform: uppercase;
                text-decoration: underline;
                color: @dark_blue;
            }
        }
    }
}

footer {
    background: @dark_blue;
    .footer_block {
        .row-flex();
        justify-content: space-between;
        p {
            margin-top: 76px;
            margin-bottom: 76px;
            color: @white;
            font-size: 20px;
            span {
                color: @bg_red;
            }
        }
        img {
            align-self: center;
        }
    }
}

.tringle {
    top:0;
    left:0;
    position: absolute;
    width: 0;
    height: 0;
	border-top: 90px solid red; 
	border-right: 90px solid transparent;	
}
.incline:after {
    position: absolute;
    top: 1.2rem;
    left: 0.4rem;
    text-indent: -5px;
    font-size: 16px;
    content: 'Popular';
    transform: rotate(-49grad);
}